<template>
  <v-app>
    <v-snackbar v-model="snackbar" timeout="2000" top>
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
    <app-banner></app-banner>
    <v-main style="min-height: 60%" v-bind:class="{ dark_background: this.$vuetify.theme.dark }">
      <v-container style="margin-top: -59px">
        <v-row dense justify="center">
          <col-block>
            <server-info class="elevation-2" v-bind:loading="loading" v-bind:query_data="data" join_open></server-info>
          </col-block>
          <v-col cols="12"></v-col>
          <col-block>
            <card title="è¾“å…¥æœåŠ¡å™¨ä¿¡æ¯">
              <v-row dense>
                <v-col cols="12" md="7">
                  <v-text-field v-model="input.ip" clearable dense label="Server IP Address" outlined></v-text-field>
                </v-col>
                <v-col class="text-center align-center hidden-sm-and-down colon" md="1">
                  <span>:</span>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field v-model="input.port" clearable dense label="Server Port" outlined></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-btn :disabled="loading" block color="primary" @click="update()">æŸ¥è¯¢</v-btn>
                </v-col>
              </v-row>
            </card>
          </col-block>
          <v-col cols="12"></v-col>
          <col-block>
            <card title="å°†æœåŠ¡å™¨å®æ—¶çŠ¶æ€åµŒå…¥ç½‘é¡µ">
              <v-row dense>
                <v-col cols="6" md="2">
                  <v-text-field v-model="width" dense label="Width" outlined required></v-text-field>
                </v-col>
                <v-col cols="6" md="2">
                  <v-text-field v-model="height" dense label="Height" outlined required></v-text-field>
                </v-col>
                <v-col md="8" class="text-sm-center">
                  <v-btn class="mr-2" v-on:click="() => {
                    this.dark = !this.dark;
                  }" :dark="!this.dark">åˆ‡æ¢ä¸»é¢˜
                  </v-btn>
                  <v-btn :class="{ 'secondary': open_join }" v-on:click="open_join = !open_join">
                    <template v-if="open_join">å…³é—­ä¸€é”®æ·»åŠ </template>
                    <template v-else>å¼€å¯ä¸€é”®æ·»åŠ </template>
                  </v-btn>
                  <v-btn class="ml-2 primary" v-on:click="copyLink" v-if="$vuetify.breakpoint.mdAndUp">å¤åˆ¶ä»£ç </v-btn>
                </v-col>
                <v-col class="mt-2" cols="12">
                  <v-textarea v-model="iframe" label="ä»£ç " outlined rows="3"></v-textarea>
                  <v-btn class="primary" block v-on:click="copyLink" v-if="$vuetify.breakpoint.smAndDown">ä¸€é”®å¤åˆ¶ä»£ç </v-btn>
                  <div v-if="$vuetify.breakpoint.mdAndUp" class="elevation-2 mb-5 grey pa-3 text-center">
                    <iframe :src="link + '&demo=true&time=' + (new Date()).valueOf()" border="0" frameborder="no"
                      :height="height" marginheight="0" marginwidth="0" scrolling=no :width="width"></iframe>
                  </div>
                </v-col>
              </v-row>
            </card>
          </col-block>
        </v-row>
      </v-container>
    </v-main>
    <app-footer class="pt-10"></app-footer>
  </v-app>
</template>


<script>
import Banner from "@/components/Banner";
import Footer from "@/components/Footer";
import ServerInfo from "@/components/ServerInfo";
import router from "@/router/index";
import axios from 'axios';
import History from "@/components/History";
import ColBlock from "@/components/ColBlock";
import Card from "@/components/Card";

export default {
  name: 'App',
  components: {
    ColBlock,
    'card': Card,
    'app-banner': Banner,
    'app-footer': Footer,
    'server-info': ServerInfo,
    'history': History
  },
  data: () => ({
    input: {
      ip: null,
      port: null
    },
    data: null,
    loading: false,
    iframe: null,
    height: '195px',
    width: '500px',
    link: '/iframe.html?ip=mc.s.aqco.top&port=3421',
    dark: false,
    snackbar: false,
    text: 'å¤åˆ¶æˆåŠŸ',
    open_join: false
  }),
  created() {
    let dark = false;


    let ip = router.currentRoute.query.ip;
    let port = router.currentRoute.query.port;
    if (ip === undefined || port === undefined || ip === null || port === null) {
      // æ­¤å¤„æ˜¯é»˜è®¤æ˜¾ç¤ºçš„æœåŠ¡å™¨çŠ¶æ€ä¿¡æ¯
      this.input.ip = 'mc.s.aqco.top';
      this.input.port = 3421;
    } else {
      this.input.ip = ip;
      this.input.port = port;
    }

    this.refreshIframe();

  },
  mounted() {
    // this.loading = true;
    this.update(this.input.ip, this.input.port);
  },
  watch: {
    width() {
      this.refreshIframe();
    },
    height() {
      this.refreshIframe();
    },
    dark() {
      this.refreshIframe();
    },
    open_join() {
      this.refreshIframe();
    }
  },
  methods: {
    async query(ip, port) {
      return await axios.get('/api?host=' + ip + ":" + port);
    },
    refreshIframe() {
      this.link = '//' + window.location.host + '/iframe.html?ip=' + this.input.ip + '&port=' + this.input.port + "&dark=" + this.dark;
      if (this.open_join) {
        this.link = this.link + '&join_open=true'
      }
      this.iframe = '<iframe frameborder="no" border="0" marginwidth="0" marginheight="0" width="' + this.width + '" height="' + this.height + '" scrolling=no src="' + this.link + '"></iframe>';
    },
    async update(ip = null, port = null) {
      this.loading = true;
      if (ip === null && port === null) {
        ip = this.input.ip;
        port = this.input.port;
      }
      let query = await this.query(ip, port);

      if (query.status === 200) {
        this.data = query.data;
        this.data.status = query.data.status !== "offline";
        this.loading = false;
      } else {
        console.log(query);
        this.loading = false;
      }

      this.refreshIframe();
    },
    copyLink() {
      this.copyText(this.iframe, (res) => {
        this.snackbar = true;
      });
    },
    // éšä¾¿ç½‘ä¸Šå¤åˆ¶ä¸€ä¸ªå¾—äº† ï¼ˆ
    copyText(text, callback) { // text: è¦å¤åˆ¶çš„å†…å®¹ï¼Œ callback: å›è°ƒ
      let tag = document.createElement('input');
      tag.setAttribute('id', 'cp_hgz_input');
      tag.value = text;
      document.getElementsByTagName('body')[0].appendChild(tag);
      document.getElementById('cp_hgz_input').select();
      document.execCommand('copy');
      document.getElementById('cp_hgz_input').remove();
      if (callback) {
        callback(text)
      }
    }
  }
}
</script>


